                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                               
export enum RemovalPolicy {
                                                                                                                                                   
  DESTROY = 'destroy',

                                                                                                                                                      
  RETAIN = 'retain',

                                                                                                                                                                                                                                                                                                                                                                                                         
  SNAPSHOT = 'snapshot',
}

export interface RemovalPolicyOptions {
                                                                                                                                                                                                                                                          
  readonly default?: RemovalPolicy;

                                                                                                          
  readonly applyToUpdateReplacePolicy?: boolean;
}
